
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { Nav, Tab, Card, Container, Button, Form, Image } from 'react-bootstrap';
import { WEBSITE_EMBED_URL } from '../../../../../../actions/api';
import { CREATE_ONSITE_FEED, SNAPUP_DETAILS_ACTION } from '../../../../../../actions/addFeedActions';
import withRouter from '../../../../../routes/WithRouter';
import ShareCodeModal from '../../../CommonComponents/ShareCodeModal';
import CardAside from '../../../CommonComponents/CardAside';

import SettingAside from './SnapupSetting/Aside';
import SettingPreview from './SnapupSetting/Preview';
import PublishAside from './PublishOption/Aside';
import PublishPreview from './PublishOption/Preview';

import './OnSiteUpload.scss'
import './_snapup.scss'
class SnapUp extends Component {
    state = {
        id: 0,
        onSiteEnable: true,
        loading: false,
        title: 'Submit your post',
        error: false,
        btnText: 'Submit',
        btnTextError: false,
        msgError: false,
        msg: 'The post(s) will be appeared after the admin approval',
        tinyUrl: `${WEBSITE_EMBED_URL}${localStorage.getItem('wallId')}/onsite-upload?wall=1`,
        uniqueCode: null,
        isLoading: false,
        showModal: false,
        dataShare: null,
        moderation: false,
        ugcSettings: null,
        firstTime: true,
        uploadQr: true,
        filterToggle: false,
        error: false,
        buttonText: "Scan QR or Tap to submit UGC",
        bgColor: "#000",
        fcColor: "#fff",
        fontSize: 8,
        qrStatus: true,
        publishOption: 'link',
        shareCodeType: 1
    }

    componentWillMount() {
        this.onLoadSettings()
    }

    componentDidUpdate(prevProps) {
        if (prevProps.snapup_details?.onsite_settings !== this.props.snapup_details?.onsite_settings) {
            this.onLoadSettings()
        }
    }

    onLoadSettings = () => {
        const { snapup_details, feeds } = this.props
        const ugcSettings = snapup_details?.onsite_settings ? snapup_details.onsite_settings : null
        const snapUpfeed = feeds?.find(x => x.networkId == 29)
        if (ugcSettings) {
            const { onsite_bg_color_wg, onsite_popup_btn_txt, onsite_popup_sub_heading, onsite_popup_title, onsite_succ_msg, onsite_status, onsite_logo } = ugcSettings;
            this.setState({ id: snapup_details?.id, ugcSettings: ugcSettings, firstTime: false, uploadQr: false, uniqueCode: snapup_details?.value1, title: onsite_popup_title, moderation: snapUpfeed?.moderation, btnText: onsite_popup_btn_txt, msg: onsite_succ_msg, onSiteEnable: onsite_status == 1 ? true : false, buttonText: "Update Connection" })
        }
        else {
            this.setState({ id: 0, title: 'Submit your post', moderation: false, btnText: 'Submit', msg: 'The post(s) will be appeared after the admin approval', onSiteEnable: true, buttonText: "Update Connection" })
        }
    }


    onChangeHandler = event => this.setState({ [event.target.name]: event.target.value, error: false, btnTextError: false, msgError: false });

    onCheckHandler = event => this.setState({ [event.target.name]: event.target.checked });

    onUploadQrChange = (state) => this.setState({ uploadQr: state })

    shareEmbedCallback = (shareEmbedEvent) => this.setState({ showModal: shareEmbedEvent, dataShare: null })

    onClickToOpen = (dataShare, shareCodeType) => event => this.setState({ showModal: true, dataShare, shareCodeType })

    closeAside = () => this.setState({ filterToggle: false })

    onFontSize = event => this.setState({ fontSize: parseInt(event > 30 ? 30 : event) })

    onQrStatus = event => this.setState({ qrStatus: event.target.checked })

    onChangeText = event => this.setState({ buttonText: event.target.value })

    publishOption = e => this.setState({ publishOption: e })


    onSubmit = () => {
        const { title, onSiteEnable, btnText, msg, moderation, ugcSettings, uploadQr, tinyUrl, id } = this.state;
        const { navigate, SNAPUP_DETAILS_ACTION } = this.props

        if (title == '') this.setState({ error: true });
        else if (btnText == '') this.setState({ btnTextError: true });
        else if (msg == '') this.setState({ msg: true });
        else {
            var obj = {
                network: 29,
                filter: 2,
                moderation: moderation ? 1 : 0,
                onsite_popup_title: title,
                onsite_popup_btn_txt: btnText,
                onsite_term_url: "https://taggbox.com/on-site-uploads-content-rights/",
                onsite_succ_msg: msg,
                onsite_status: onSiteEnable ? 1 : 0,
                wallId: localStorage.getItem('wallId')
            };
            if (ugcSettings) obj = { ...ugcSettings, ...obj, id }
            else {
                obj = {
                    id,
                    ...obj,
                    onsite_qr_code_btn_text: "Scan QR code or click here to submit reviews/rating",
                    onsite_btn_color: "#000000",
                    onsite_btn_txt_color: "#ffffff",
                    onsite_link_font_btn: "Lato",
                    onsite_css_font_btn: "Lato",
                    onsite_font_varient_btn: "",
                    btn_fontSize: 16,
                    onsite_button_status: 1,
                    onsite_qr_code_status: 1,
                    onsite_thank_txt_color: "#000000",
                    onsite_link_font_msg: "Default",
                    onsite_css_font_msg: "Default",
                    onsite_font_varient_msg: "",
                    onsite_thank_fontSize: 16,
                    choose_content_category: 1,
                }
            }
            this.setState({ loading: true, error: false, btnTextError: false, msgError: false });
            CREATE_ONSITE_FEED(obj, () => { navigate(`/content/managefeed/${localStorage.getItem('wallId')}`) }, uploadQr, tinyUrl)
        }

    }


    render() {
        const { title, onSiteEnable, loading, btnText, msg, btnTextError, msgError, shareCodeType, showModal, tinyUrl, moderation, firstTime, dataShare, filterToggle, error, buttonText, bgColor, fcColor, fontSize, qrStatus, publishOption, uniqueCode } = this.state;
        return <Container>
            <Card>
                <Card.Body className='p-0'>
                    <Tab.Container defaultActiveKey="snapup">
                        <div className="p-0 d-flex w-10">
                            <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                                <Card className='a_header_'>
                                    <Card.Header className='py-1'>
                                        <div className="d-flex flex-wrap align-items-center">
                                            <div className="symbol symbol-40 flex-shrink-0 me-4">
                                                <Image src={baseUrl(`media/icons/social-icons-square/onsite-upload.svg`)} fluid />
                                            </div>
                                            <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2">
                                                <h3 className="mb-0 h4">SnapUp</h3>
                                                <p className="text-muted mb-0 fw-medium">Add your SnapUp</p>
                                            </div>
                                        </div>
                                    </Card.Header>
                                    <Card.Body className='bg-gray-100 aside_body w-100 p-0'>
                                        <Tab.Content>
                                            <Tab.Pane eventKey="snapup" className='p-4'>
                                                <SettingAside onCheckHandler={this.onCheckHandler} onChangeHandler={this.onChangeHandler} {...this.state} />
                                            </Tab.Pane>
                                            <Tab.Pane eventKey="publish">
                                                <Tab.Container id="publish_aside" defaultActiveKey="link" onSelect={this.publishOption}>
                                                    <Nav variant="underline" className='justify-content-start border-bottom'>
                                                        <Nav.Item className='ms-2'>
                                                            <Nav.Link eventKey="link">Link</Nav.Link>
                                                        </Nav.Item>
                                                        <Nav.Item className='ms-2'>
                                                            <Nav.Link eventKey="website">Website</Nav.Link>
                                                        </Nav.Item>
                                                    </Nav>
                                                    <Tab.Content>
                                                        <PublishAside onClickToOpen={this.onClickToOpen} onChangeText={this.onChangeText} onBgColorChange={(clr) => this.setState({ bgColor: clr })} onFcColorChange={(clr) => this.setState({ fcColor: clr })} onQrStatus={this.onQrStatus} onFontSize={this.onFontSize}  {...this.state} />
                                                    </Tab.Content>
                                                </Tab.Container>
                                            </Tab.Pane>
                                        </Tab.Content>
                                    </Card.Body>
                                </Card>
                            </CardAside>

                            <Card className="preview_ p_header_ p_footer_">
                                <Card.Header className='py-0 align-items-end'>
                                    <Nav variant="underline" className='justify-content-center'>
                                        <Nav.Item className='me-2'>
                                            <Nav.Link eventKey="snapup">SnapUp Settings</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item className='me-2'>
                                            <Nav.Link eventKey="publish">Publish Options</Nav.Link>
                                        </Nav.Item>
                                    </Nav>
                                    <div className='my-5'>
                                        <Button id="toggle_aside" variant='link' size='sm' className='d-flex d-xl-none' onClick={() => this.setState({ filterToggle: !filterToggle })}>
                                            Menu
                                            <FontAwesomeIcon icon={'sliders'} iconClass={'ms-2 lh-base'} />
                                        </Button>
                                    </div>
                                </Card.Header>
                                <Card.Body>
                                    <Tab.Content>
                                        <Tab.Pane eventKey="snapup">
                                            <SettingPreview {...this.state} />
                                        </Tab.Pane>
                                        <Tab.Pane eventKey="publish">
                                            {publishOption == 'link' ?
                                                <div id="link_preview">
                                                    <SettingPreview {...this.state} />
                                                </div> : null}
                                            {publishOption == 'website' ?
                                                <div id="web_preview">
                                                    <PublishPreview {...this.state} onUploadQrChange={this.onUploadQrChange} onClickToOpen={this.onClickToOpen} />
                                                </div> : null}
                                        </Tab.Pane>
                                    </Tab.Content>
                                </Card.Body>
                                <Card.Footer className='text-end h-auto' style={{ minHeight: 66 }}>
                                    <div className='d-flex justify-content-between flex-wrap'>
                                        <Form.Group className='my-3'>
                                            <Form.Check type="checkbox" className='text-start' id="manually_app_posts" label={`Manually approve posts before making them public?`} checked={moderation} onChange={(e) => this.setState({ moderation: e.target.checked })} />
                                        </Form.Group>
                                        <div className='d-flex justify-content-end align-items-center p-2'>
                                            <Button variant='secondary' className='me-2' onClick={() => this.props.navigate(`/content/addfeed/${localStorage.getItem('wallId')}`)} >Back</Button>
                                            <Button className={`${loading ? 'spinner' : ""}`} id="create_feed" onClick={this.onSubmit}>{firstTime ? "Create" : 'Update'} Feed</Button>
                                        </div>
                                    </div>
                                </Card.Footer>
                            </Card>
                        </div>
                    </Tab.Container>
                    {showModal ? <ShareCodeModal type={6} from={'snapUp'} onClose={(value) => this.setState({ showModal: value })} sharecode={{ 'emailCmp': dataShare }} shareUrl={dataShare} isShare={shareCodeType == 2 ? false : true} codeId={'#buttonEmbedCode'} /> : <></>}
                </Card.Body>
            </Card>
        </Container>
    }
}
const mapStateToProps = (state) => {
    return {
        snapup_details: state.addFeedReducer?.details,
        feeds: state.manageFeeds?.feeds,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        SNAPUP_DETAILS_ACTION: (callback) => dispatch(SNAPUP_DETAILS_ACTION(callback)),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SnapUp))