import React, { useEffect, useMemo } from 'react';
import { Row } from 'react-bootstrap';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Container } from 'react-bootstrap';
import { GET_ADD_FEED, GET_NETWORK_INFO_BY_ID, FETCH_CONNECTED_ACCOUNTS } from '../../../../../actions/moderationActions';
import { GET_NETWORK_NAME } from '../../../../../utils';
import { SNAPUP_DETAILS_ACTION } from '../../../../../actions/addFeedActions';
import { GET_NETWORK_DATA_BY_ID } from '../../../../../utils';
import SnapUp from './SnapUp';
import FeedBody from './FeedBlock/FeedBody';
import { Loader } from '../../../Main/CommonComponents/Loader'

const CreateFeed = () => {
    const { networkId } = useParams();
    const dispatch = useDispatch();
    const { networkDetails, addFeedNetworks, details } = useSelector(state => state.addFeedReducer);
    useEffect(() => {
        dispatch(GET_ADD_FEED());
        dispatch(GET_NETWORK_INFO_BY_ID(parseInt(networkId)));
        dispatch(FETCH_CONNECTED_ACCOUNTS(networkId));

        if (networkId === '18') dispatch(FETCH_CONNECTED_ACCOUNTS(2));
        if (networkId === '29') dispatch(SNAPUP_DETAILS_ACTION());
    }, [networkId, dispatch]);

    useEffect(() => {
        // Check if networkDetails.id is null/undefined and addFeedNetworks is available
        if (!networkDetails?.id && addFeedNetworks?.networks?.length) {
            dispatch(GET_NETWORK_INFO_BY_ID(parseInt(networkId)));
        }
    }, [addFeedNetworks, networkDetails]);

    const network = useMemo(() => GET_NETWORK_DATA_BY_ID(networkId), [networkId, addFeedNetworks]);

    return (<div className='d-flex flex-column flex-column-fluid full-content'>
        <div className="subheader">
            <Container fluid>
                <div className='f-between'>
                    <h1 className="me-2 h4 my-3">Create {GET_NETWORK_NAME(network ? network.id : '')} Feed</h1>
                </div>
            </Container>
        </div>
        <Container>
            {networkDetails?.id ? <>
                {networkId !== '29' ? (
                    <FeedBody networkId={networkId} network={network} networkDetails={networkDetails} />
                ) : (
                    details && networkDetails?.networkTab && (
                        <SnapUp onBack={() => { }} onUGCUploadCollection={null} feedInfo={networkDetails.networkTab} />
                    )
                )}
            </> : <Row className='f-center' style={{ minHeight: 'calc(100vh - 50px)' }}><Loader /></Row>}
        </Container>
    </div>
    );
};

export default CreateFeed;